/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  color: inherit;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.ttf");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.ttf");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-SemiBold.ttf");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-ExtraBold.ttf");
  font-weight: 800;
  font-style: normal;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  min-width: 479.98px;
  font-family: "Open Sans", sans-serif;
  color: #ffffff;
  background-color: #3b434c;
}
body a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1170px;
  padding: 0 15px;
}
@media (max-width: 991.98px) {
  .container {
    max-width: 970px;
  }
}
@media (max-width: 767.98px) {
  .container {
    max-width: 740px;
  }
}
@media (max-width: 575.98px) {
  .container {
    max-width: none;
  }
}

.bg-container {
  margin: 0 auto;
  background-image: linear-gradient(rgba(59, 67, 76, 0.9), rgba(59, 67, 76, 0.8)), url("../img/backgrounds/slider-img.png");
  background-size: cover;
}

.section-newsletter {
  background-image: linear-gradient(rgba(59, 67, 76, 0.9), rgba(59, 67, 76, 0.8)), url("../img/backgrounds/newsletter.png");
  background-size: cover;
}

.section-contact {
  background-image: linear-gradient(rgba(59, 67, 76, 0.9), rgba(59, 67, 76, 0.8)), url("../img/backgrounds/contact.png");
  background-size: cover;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 210px;
  height: 50px;
  padding: 8px;
  background-color: transparent;
  border-radius: 10px;
  border: 1px solid #6e757b;
  font-family: "Open Sans";
  font-weight: 600;
  color: white;
}
.btn:hover {
  border: 1px solid #f7600e;
  background-color: #f7600e;
  transition: 0.3s all ease;
}

nav {
  overflow: hidden;
}

.navbar {
  padding-top: 44px;
  padding-bottom: 44px;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 991.98px) {
  .navbar {
    padding-top: 35px;
    padding-bottom: 35px;
  }
}
@media (max-width: 767.98px) {
  .navbar {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.logo {
  padding-left: 30px;
}
@media (max-width: 767.98px) {
  .logo {
    padding-left: 20px;
  }
}
@media (max-width: 575.98px) {
  .logo {
    padding-left: 10px;
  }
}

.menu {
  display: flex;
  align-items: center;
}
@media (max-width: 575.98px) {
  .menu__item span {
    display: none;
  }
}
.menu__item-link {
  display: flex;
  align-items: center;
  font-size: 16px;
}
.menu__item-link svg {
  margin-left: 14px;
}
.menu__item-link:hover {
  color: #f7600e;
  transition: 0.3s all ease;
}
.menu__item-link:hover .path-fill {
  fill: #f7600e;
  transition: 0.3s all ease;
}

.slider {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.slider__title {
  padding-top: 120px;
  font-size: 70px;
  font-weight: 800;
}
.slider__title span {
  color: #f7600e;
}
@media (max-width: 991.98px) {
  .slider__title {
    padding-top: 100px;
    font-size: 56px;
  }
}
@media (max-width: 767.98px) {
  .slider__title {
    padding-top: 80px;
    font-size: 48px;
  }
}
@media (max-width: 575.98px) {
  .slider__title {
    padding-top: 60px;
    font-size: 32px;
  }
}
@media (max-width: 479.98px) {
  .slider__title {
    padding-top: 40px;
    font-size: 28px;
  }
}
.slider__subtitle {
  text-align: center;
  padding-top: 20px;
  font-size: 25px;
  font-weight: 300;
  letter-spacing: 0.3px;
  line-height: 1.6;
}
@media (max-width: 991.98px) {
  .slider__subtitle {
    font-size: 16px;
    max-width: 550px;
  }
}
@media (max-width: 767.98px) {
  .slider__subtitle {
    max-width: 400px;
  }
}
@media (max-width: 575.98px) {
  .slider__subtitle {
    font-size: 14px;
    max-width: none;
  }
}
.slider__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
}
.slider__buttons :first-child {
  margin-right: 25px;
}
@media (max-width: 767.98px) {
  .slider__buttons {
    padding-top: 50px;
  }
}
@media (max-width: 575.98px) {
  .slider__buttons {
    flex-wrap: wrap;
  }
  .slider__buttons :first-child {
    margin-right: 0px;
  }
}
@media (max-width: 575.98px) {
  .slider__btn {
    flex: 1 1 100%;
    min-width: 100%;
    margin-top: 10px;
  }
}
.slider__btn-arrow {
  padding-top: 194px;
  padding-bottom: 50px;
}
@media (max-width: 991.98px) {
  .slider__btn-arrow {
    padding-top: 120px;
  }
}
@media (max-width: 767.98px) {
  .slider__btn-arrow {
    padding-top: 100px;
  }
}
@media (max-width: 575.98px) {
  .slider__btn-arrow {
    padding-top: 60px;
  }
}

.section-services {
  background: #ffffff;
}

.services {
  display: flex;
}
@media (max-width: 991.98px) {
  .services {
    flex-wrap: wrap;
  }
}
.services__item {
  flex: 1;
  display: flex;
  flex-direction: column;
  color: #3b434c;
  text-align: center;
  padding: 100px 0px;
  margin-right: 32px;
}
@media (max-width: 1199.98px) {
  .services__item {
    align-items: center;
    margin-right: 0px;
  }
}
@media (max-width: 991.98px) {
  .services__item {
    flex: 1 1 48%;
    padding: 60px 0px;
  }
}
@media (max-width: 767.98px) {
  .services__item {
    padding: 40px 0px;
  }
}
@media (max-width: 479.98px) {
  .services__item {
    flex: 1 1 100%;
  }
}
.services__item-img {
  margin: 0px 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  width: 150px;
  border: 1px solid #c4c6c9;
  border-radius: 50%;
  user-select: none;
}
@media (max-width: 1199.98px) {
  .services__item-img {
    margin: 0;
  }
}
.services__item-title {
  font-weight: 600;
  padding-top: 30px;
}
.services__item-text {
  padding-top: 20px;
  padding-bottom: 5px;
  line-height: 26px;
  font-size: 14px;
}

.newsletter {
  display: flex;
  padding: 100px 0px;
}
.newsletter form {
  width: 75.5%;
  margin: auto;
  display: flex;
}
@media (max-width: 767.98px) {
  .newsletter form {
    flex-wrap: wrap;
  }
}
.newsletter__input {
  display: flex;
  width: 100%;
  padding: 15px 15px;
  padding-left: 20px;
  outline: none;
  border: none;
  border-radius: 10px;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 300;
}
.newsletter__input:focus {
  outline: 1px solid #f7600e;
}
@media (max-width: 767.98px) {
  .newsletter__input {
    margin-right: 0;
    flex: 0 1 100%;
  }
}
.newsletter__btn {
  display: flex;
  width: 268px;
  font-size: 16px;
  border: 1px solid #f7600e;
  background-color: #f7600e;
  cursor: pointer;
}
@media (max-width: 991.98px) {
  .newsletter__btn {
    width: 210px;
  }
}
@media (max-width: 767.98px) {
  .newsletter__btn {
    flex: 0 1 100%;
    margin-top: 10px;
    height: 45px;
  }
}

.form-label {
  flex-grow: 1;
  margin-right: 30px;
}
@media (max-width: 767.98px) {
  .form-label {
    margin-right: 0;
  }
}

.section-works {
  background: #ffffff;
  padding: 95px 0px;
}
@media (max-width: 991.98px) {
  .section-works {
    padding: 80px 0px;
  }
}
@media (max-width: 767.98px) {
  .section-works {
