  }
}

.works {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.works__title {
  color: #3b434c;
  font-size: 35px;
}
.works__title span {
  color: #f7600e;
}
.works__subtitle {
  color: #3b434c;
  max-width: 460px;
  padding-top: 20px;
  text-align: center;
  line-height: 26px;
}
.works__items {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 72px;
}
@media (max-width: 1199.98px) {
  .works__items {
    justify-content: space-around;
  }
}
@media (max-width: 991.98px) {
  .works__items {
    padding-top: 50px;
  }
}
@media (max-width: 767.98px) {
  .works__items {
    padding-top: 30px;
  }
}
.works__item {
  position: relative;
  display: block;
  margin-top: 25px;
  margin-right: 30px;
}
.works__item:nth-child(3n) {
