  z-index: 3;
  background: rgba(247, 96, 14, 0.85);
  border-radius: 10px;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 75px 50px;
}
.works__item-title {
  font-weight: 600;
  font-size: 25px;
  line-height: 40px;
}
.works__item-text {
  padding-top: 15px;
  padding-bottom: 45px;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
}
.works__item-btn {
  border: 1px solid #ffffff;
  color: #ffffff;
}
.works__item-btn:hover {
  background: #ffffff;
  color: #f7600e;
}
.works__btn {
  margin-top: 100px;
  color: #3b434c;
  border: 1px solid #c4c6c9;
}
.works__btn:hover {
  color: #ffffff;
}
@media (max-width: 991.98px) {
  .works__btn {
    margin-top: 80px;
  }
}
@media (max-width: 767.98px) {
  .works__btn {
    margin-top: 50px;
  }
}

.section-contact {
  padding-top: 95px;
  padding-bottom: 140px;
}
@media (max-width: 991.98px) {
  .section-contact {
    padding: 80px 0px;
  }
}
@media (max-width: 767.98px) {
  .section-contact {
    padding: 50px 0px;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact__title {
  color: #ffffff;
  font-size: 35px;
}
.contact__title span {
  color: #f7600e;
}
.contact__subtitle {
  color: #ffffff;
  max-width: 460px;
  padding-top: 20px;
  text-align: center;
  line-height: 26px;
}
.contact form {
  padding-top: 95px;
  width: 73%;
  margin: auto;
}
@media (max-width: 991.98px) {
  .contact form {
    padding: 80px 0px;
  }
}
@media (max-width: 767.98px) {
  .contact form {
    padding: 50px 0px;
  }
}
.contact__form-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 991.98px) {
  .contact__form-header {
    flex-wrap: wrap;
  }
}
.contact__form-input {
  flex: 1;
  outline: none;
  border: none;
  border-radius: 10px;
  padding: 15px 20px;
  color: #3b434c;
  font-family: "Open Sans";
  font-size: 16px;
}
.contact__form-input:first-child {
