  }
}
.contact__form-text {
  resize: none;
  display: flex;
  width: 100%;
  height: 200px;
  padding-top: 15px;
  padding-left: 20px;
  margin-top: 30px;
  border-radius: 10px;
  border: none;
  outline: none;
  color: #3b434c;
  font-family: "Open Sans";
  font-size: 16px;
  font-weight: 300;
}
.contact__form-text:focus {
  outline: 1px solid #f7600e;
}
.contact__buttons {
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767.98px) {
  .contact__buttons {
    flex-wrap: wrap;
  }
}
.contact__btn {
  cursor: pointer;
  font-size: 16px;
}
@media (max-width: 767.98px) {
  .contact__btn {
    flex: 0 1 100%;
  }
}
.contact__icons {
  margin-top: 5px;
}
.contact__icons a:not(:last-child) {
  margin-right: 17px;
}
@media (max-width: 991.98px) {
