    margin-top: 20px;
    flex: 0 1 100%;
  }
}
.contact__btn {
  color: #ffffff;
  background: #f7600e;
  border: #f7600e;
}

footer {
  width: 100%;
}

.copy {
  padding-top: 45px;
  letter-spacing: 0.4px;
  padding-bottom: 85px;
}